export default {
	data() {
		return {
			listData:[],
			
			items:[
			{	
				value:"1",
				name:"研发"
			},
			{
				value:"2",
				name:"产品",
			},
			{
				value:"3",
				name:"设计"
			},
			{
				value:"4",
				name:"运营"
			},
			{
				value:"5",
				name:"销售"
			},
			{
				value:"6",
				name:"市场"
			}
			],
			
			cities:[
				{
					value:"1",
					cityname:"杭州",
				},
				{
					value:"2",
					cityname:"上海"
				},
				{
					value:"3",
					cityname:"北京"
				}, 
				{
					value:"4",
					cityname:"广州"
				},
				{
					value:"5",
					cityname:"深圳"
				},
				{
					value:"6",
					cityname:"成都"
				},
					
			]
			
		}
	},
	
	onLoad:function(e){
		let th=this;
		uni.request({
			url:'http://zygh.store/api/company/腾讯/job',
			success:function(res){
				th.listData=res.data.data.data;
				console.log(th.listData);	
			}
		})
		
	}, 
	
	
	
	
	methods: {
		checkboxChange1:function(e){
		var items=this.items,
			values=e.detail.value;
		for(var i=0,lenI=items.length;i<lenI;++i){
		    const item=items[i];
			if(values.includes(item.value)){
				this.$set(item,'checked',true)
				console.log(item.name);
				let th=this;
				 uni.request({
					url:'http://zygh.store/api/company/腾讯/job?category[]='+item.name,
					success:function(res){
						th.listData=res.data.data.data; 
						console.log(th.listData);
					}
				}) 
			}else{
				this.$set(item,'checked',false)
			}
		}
		
	},
	
	checkboxChange2:function(e){
		var items=this.cities,
			values=e.detail.value;
		for(var i=0,lenI=items.length;i<lenI;++i){
		    const item=items[i];
			if(values.includes(item.value)){
				this.$set(item,'checked',true)
				console.log(item.cityname);	
				let th=this;
				uni.request({
					url:'http://zygh.store/api/company/腾讯/job?place[]='+item.cityname,
					success:function(res){
						th.listData=res.data.data.data;
						console.log(th.listData);
					}
				})
			}else{
				this.$set(item,'checked',false)
			}
		}
	},
	gojobs:function(e){
		uni.navigateTo({
			url: '/pages/position_details/position_details',
		});
	},
}	
}
